import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ServerHelperProvider } from '../../providers/server-helper/server-helper';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';



/*
  Generated class for the ServerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ServerProvider {
  private JSONuserdata: any = null;
  private BaseUrl = "https://app.azuzawealth.com/?iCd=";
  private network = 0;
  private httpClient = null;
  private httpRequest  = null;

  constructor(private httpService: HttpClient, private requestService: ServerHelperProvider) {
   
    this.httpClient = httpService;
    this.httpRequest = requestService
  }



  public async doGetRequest(resource: string, token:string = null, params: {} = false): Promise<any>{
    
    let promise = new Promise((resolve, reject) => {

      this.processGetRequest(resource, token, params).subscribe(
        data => { 
          console.log("returning data");
          resolve(data);
        },
        err => {
          console.log("HTTP ERROR: " + err);
          reject(false);
        }
     );	

    });

    return promise;
    
  }

  //post request takes params in JSON array list, 
  public async doPostRequest(resource: string, token:string = null, params: {} = false) {

    if (!params) {
      return false;
    }

    let response = null;
    this.processPostRequest(resource, token, params).subscribe(
      data => { 
        response = data;
      },
      err => {
        response = false;
        console.log("HTTP ERROR: " + err);
      }
   );	   

  

    return await response;
  }




  private makeUrl(resource: string, token:string) {

    token = token ? token : "";

    return this.BaseUrl + resource + "&token=" + token + "&iCn=" + this.network;
  }


  /*
  * @Returns BODY ONLY - JSON data
  */
 private processGetRequest(resource: string, token: string, params:{}): Observable<any> {
  let options = {
    observe: 'body',
    responseType: 'json',
    params: params, /* Pass URL params here */
    //credentials: '' /* Pass login credentials here */
  };
  return this.httpClient.get(this.makeUrl(resource, token), options);

}

/*
* @Returns BODY ONLY, JSON data
*/
private processPostRequest(resource: string, token:string, params: {}): Observable<any> {

  let headers = new Headers();
  let httpHeaders = new HttpHeaders({
    'Content-Type': 'application/x-www-form-urlencoded',
    'Cache-Control': 'no-cache'
  });
  let options = {
    headers: httpHeaders,
    observe: 'body',
    responseType: 'json'
  };

  return this.httpClient.post(this.makeUrl(resource, token), JSON.stringify(params), options);

}

}



