  // upload doc api

  uploadDocs(file, name, type1, type2, type3) {
    // alert(address + "dddddd");

    // alert(file);
    return new Promise((resolve, reject) => {
      const uploadData = new FormData();
      uploadData.append(type1, file);
      uploadData.append(type2, name);
      uploadData.append(type3, name);
      uploadData.append('description[id]', 'KYC');
      uploadData.append('group', 'front');


      let httpOptions = {
        headers: new HttpHeaders({
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'POST, GET, OPTIONS, PUT'
        })
      };

      var token = JSON.parse(localStorage.getItem('token'));
      var url = this.BaseUrl + "KYCSave" + "&token=" + token + "&iCn=" + this.network;

      this.http1.post(url, uploadData, httpOptions)
        .subscribe(res => {
          resolve(res);
        }, (err) => {
          reject(err);
        });
    });
  }



