import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';
import { PipesModule } from '../pipes/pipes.module';
import { CalendarModule } from "ion2-calendar";

import { AzuzaApp } from './app.component';

import { MessageService } from "../providers/message-service-mock";
import { PropertyService } from "../providers/property-service-mock";
import { BrokerService } from "../providers/broker-service-mock";
import { InvoiceService } from "../providers/invoice-service-mock";
import { ChatService } from "../providers/chat-service-mock";
import { UserProvider } from '../providers/user/user';
import { GlobalsProvider } from '../providers/globals/globals';

import {TranslateLoader, TranslateModule} from "@ngx-translate/core";
import {TranslateHttpLoader} from "@ngx-translate/http-loader";
import { ServerProvider } from '../providers/server/server';
import { ServerHelperProvider } from '../providers/server-helper/server-helper';
import { CountryListProvider } from '../providers/country-list/country-list';

@NgModule({
  declarations: [
    AzuzaApp
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: (HttpLoaderFactory),
          deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(AzuzaApp, {
      preloadModules: true,
      iconMode: 'md',
      mode: 'md'
    }),
    IonicStorageModule.forRoot({
      name: '__azuzaDB',
      driverOrder: ['sqlite',  'indexeddb', 'websql']
    }),
    PipesModule,
    CalendarModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    AzuzaApp
  ],
  providers: [
    PropertyService,
    BrokerService,
    MessageService,
    InvoiceService,
    ChatService,
    UserProvider,
    GlobalsProvider,
    ServerProvider,
    ServerHelperProvider,
    CountryListProvider,
    // { provide: LocationStrategy, useClass: PathLocationStrategy },
    // { provide: APP_BASE_HREF, useValue : '/' },
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}




